import React, { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Constant from '../../../../../Constant';

import WithSidebar from '../../../../../Container/WithSidebar/WithSidebar';
import AuthorInfo from '../../../../../Components/AuthorInfo/AuthorInfo';
import Swift from '../Swift';
import SwiftCode from '../../../../../Components/SwiftCode/SwiftCode';

const Enumerations = (props) => {
  return (
    <Fragment>
      <Helmet>
        <title>
          What is Enumerations and When you should use it | Swift | Appdevelopment
        </title>
        <meta
          name="description"
          content="Enumerations is a list of related value and it allow you to work with 
          those values in a safe way. Safe way means that it prevent you from passing the incorrect
          value or etc. To create enumeration, you use the keyword enum."
        />
        <meta
          name="keywords"
          content="enum,overview,create,line,number,iOS,swift,tutorial,video,reference"
        />
        <meta name="author" content="Wes Chua Swee Kwang" />
        <link
          href={Constant.address + '/reference/swift/arithemtic'}
          rel="canonical"
        />
        <link href={Constant.address} rel="home" />
        <meta name="robots" content="index, follow" />
      </Helmet>

      <WithSidebar
        data={Swift}
        headerTitle="Swift Reference"
        headerURL="/reference/swift"
        activate={[3, 0]} // Section, Row
        state={props.state}
      >
        <h1 className="subHeader">Enumeration</h1>
        <h2 className="header">What is a Enumeration and when you should use it?</h2>
        <AuthorInfo version="Xcode 12.2, Swift 5.3" />
        <p>
          Enumeration is a group of related values and it allow you to work with these value in a safer way.
          To better understand, let's take the following example and we wants to find the meteorological 
          season of the month depending on the input and we can use the following code:
        </p>
        <br/>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">func</span> <span className="blue">findSessions</span>(<span className="darkpurple">_</span> month: <span className="purple">String</span>) -&gt; <span className="purple">String</span>&#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">switch</span> month &#123; <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="red">"March"</span>, <span className="red">"April"</span>, <span className="red">"May"</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Spring"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="red">"June"</span>, <span className="red">"July"</span>, <span className="red">"August"</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Summer"</span><br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="red">"September"</span>, <span className="red">"October"</span>, <span className="red">"November"</span> <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Autumn"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="red">"December"</span>, <span className="red">"January"</span>, <span className="red">"February"</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Winter"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">default</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"invalid"</span> <br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <br/>
        <p>Now, you want to find the sessions of October and you call the function as shown below. However,
        There is a likely that you might mistype it for example, you type "Octr" instead of "October". 
        Furthermore, you might short form of it and type "Oct" instead. Wouldn't it be good if you have a 
        better way to do it? You can use <strong>enumeration</strong> to solve this problem.</p>
        <br/>

        <SwiftCode
          code={
            <Fragment>
              findSessins(<span className="red">"October"</span>)<br/>
              findSessins(<span className="red">"Octr"</span>)<br/>
              findSessins(<span className="red">"Oct"</span>)<br/> 
            </Fragment>
          }
          result= {
            <Fragment>
              Autumn <br/>
              invalid <br/>
              invalid <br/>
            </Fragment>
          }
        />
        <h3 className="header2">Implemention of enumeration</h3>
        <h3>1. Create Enum</h3>
        <p>Now, we should create an enumeration to solve the problem above. To create an enumeration
           you use the keyword <strong>enum</strong> and then you list out all the possible values as 
           <strong>cases</strong> clauses as shown below:</p>

          <SwiftCode
          code={
            <Fragment>
              <mark><span className="darkpurple">enum</span></mark> <span className="purple">Month</span> &#123; <br />
              &nbsp;&nbsp;&nbsp;<mark><span className="darkpurple">case</span></mark> january <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> february <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> march <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> april <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> may <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> june<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> july<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> august<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> september<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> october<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> november<br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> december<br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <p>The code above creats an enumeration called Month with 12 values. In convention, <strong>the name 
           of enumeration's first letter is an upper case</strong> just like as class or struct and 
           <strong> the value start with a lower case just </strong>like a property.</p>
        <p>To make life simple for developer like you and I. You can simplify the code into one line by 
           using <strong>comma to separate each value</strong>.</p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">enum</span> <span className="purple">Month</span> &#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> january, february, march, april, may, june, july, august, september, october, november, december<br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <h3>2. Using the enumeration in the Function</h3>
        <p>Using the function, findSessions we created just now. You can rewrite by using enumeration. So, it uses enumeration values 
           to match instead of the string. <i>Remember to change the data type of string to Month.</i> </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">func</span> <span className="blue">findSessions</span>(<span className="darkpurple">_</span> month: <span className="purple">Month</span>) -&gt; <span className="purple">String</span>&#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">switch</span> month &#123; <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <mark><span className="purple">Month</span>.March</mark>, <span className="purple">Month</span>.April, <span className="purple">Month</span>.May: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Spring"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="purple">Month</span>.June, <span className="purple">Month</span>.July, <span className="purple">Month</span>.August: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Summer"</span><br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="purple">Month</span>.September, <span className="purple">Month</span>.October, <span className="purple">Month</span>.November<br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Autumn"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <span className="purple">Month</span>.December, <span className="purple">Month</span>.January, <span className="purple">Month</span>.February: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Winter"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">default</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"invalid"</span> <br/>
              &#125;
              <br />
            </Fragment>
          }
        />

        <h3>3. Remove enumeration name</h3>

        <p>Since Swift is stronly-typed and uses type inference you can remove the enumeration name in places and keep the dot 
        prefix and the compiler will knows the type, as shown below: </p>

        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">func</span> <span className="blue">findSessions</span>(<span className="darkpurple">_</span> month: <span className="purple">Month</span>) -&gt; <span className="purple">String</span>&#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">switch</span> month &#123; <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> <mark>.March</mark>, .April, .May: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Spring"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .June, .July, .August: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Summer"</span><br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .September, October, .November<br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Autumn"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .December, January, .February: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Winter"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">default</span>: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"invalid"</span> <br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <br/>

        <h3>4. Remove default case</h3>
        <p>Moreover, you can remove the default case when you use enumeration because enumerations have a limited set of value 
           and it is impossible to be other type of value rather then Month and compiler will not warn you. Look back, if you case 
           pattern is a String elements, then you need to have a <strong>default</strong> because there a infinite amount of value can be 
           use.</p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">func</span> <span className="blue">findSessions</span>(<span className="darkpurple">_</span> month: <span className="purple">Month</span>) -&gt; <span className="purple">String</span>&#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">switch</span> month &#123; <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .March, .April, .May: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Spring"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .June, .July, .August: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Summer"</span><br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .September, October, .November<br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Autumn"</span> <br/>
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> .December, January, .February: <br/>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="darkpurple">return</span> <span className="red">"Winter"</span> <br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <br/>
        <p>By using enumeration and removing the default as shown above. Your code become more clean and more readable. 
           Another huge benefit is that, in the future, when someone add a new value to the enum, the compiler will flag this out that 
           there are other Month enumeration in this switch statement as it is incomplete and you are require to handle it.</p>

        <h3 className="header2">Raw Values</h3>
        <p>There is a problem here. For example, we want the extract the raw value of it which is "april" and the 
           <strong>data type is a Month.Type</strong> and <strong>not a String.Type</strong>. How can we do that?</p>
        <SwiftCode
          code={
            <Fragment>
            type(of: Month.<span className="blue">april</span>)
            </Fragment>
          }
          result= "__lldb_expr_12.Month.Type"
        />
        <p>Enumeration allows you to link a default value or <i>raw values</i>. Take a look a the following example, 
           when I specify an integer backs the enumerations by first declaring it with <strong>: datatype</strong> and then,
           you using an <strong>equal sign to assign the raw value</strong> to it like the example below. 
           <i> (Enumerations are flexible and you can specify any raw values types like Int, Float, Bool and etc).</i></p>
           <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">enum</span> <span className="blue">Month</span><mark>: <span className="purple">String</span></mark> &#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">case</span> january <mark>= <span className="red">"january"</span></mark>, february <mark>= <span className="red">"february"</span></mark>, march <mark>= <span className="red">"march"</span></mark>, april <mark>= <span className="red">"april"</span></mark>, may <mark>= <span className="red">"may"</span></mark>, june <mark>= <span className="red">"june"</span></mark>, july <mark>= <span className="red">"july"</span></mark>, august <mark>= <span className="red">"august"</span></mark>, september <mark>= <span className="red">"september"</span></mark>, october <mark>= <span className="red">"october"</span></mark>, november <mark>= <span className="red">"november"</span></mark>, december <mark>= <span className="red">"december"</span></mark><br/>
              &#125;
              <br />
            </Fragment>
          }
        />
        <h3>1. Accessing the raw value</h3>
        <p>Enumeration come with a very handy <strong>rawValue</strong> property. Which allows you to use it like a normal 
           String, Int, Float and etc.</p>
        <SwiftCode
          code={
            <Fragment>
            type(of: Month.<span className="blue">april</span>.<span className="blue">rawValue</span>)
            </Fragment>
          }
          result= "String.Type"
        />
        <h3>2. Initializing the raw value</h3>
        <p>Instead of using the dot notation (Month.april) to create the enumeration value. You can use the 
        raw value to instantiate an enumeration value with an initializer. You can use <strong>init(rawValue: )</strong>.
        Remember, because we are submitting an raw value when you use it. There is a possible that you submitted a wrong 
        raw value. Therefore, this initializer will return an <strong>optional</strong>. If thing go worng, it will return nil.</p>

        <p>Look at the example, below, which require to force unwrap the optional in order to access the enum.</p>
        <SwiftCode
          code={
            <Fragment>
            <span className="darkpurple">let</span> <span className="blue">currentMonth</span> = <span className="blue">Month</span>(rawValue: <span className="red">"january"</span>)<br/>
            <span className="purple">print</span>( <span className="blue">currentMonth</span>! ) 
            </Fragment>
          }
          result= "january"
        />

      <h3 className="header2">Associated Values</h3>
      </WithSidebar>
    </Fragment>
  );
};

export default Enumerations;
