import React, { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Constant from '../../../../../Constant';

import WithSidebar from '../../../../../Container/WithSidebar/WithSidebar';
import AuthorInfo from '../../../../../Components/AuthorInfo/AuthorInfo';
import Swift from '../Swift';
import SwiftCode from '../../../../../Components/SwiftCode/SwiftCode';

const Arrays = (props) => {
  return (
    <Fragment>
      <Helmet>
        <title>
          How to create Range or ClosedRange in Swift | Collection Data type in
          Swift{' '}
        </title>
        <meta name="description" content=" " />
        <meta
          name="keywords"
          content="sets,collection,swift,data,type,overview,create,line,number,iOS,swift,tutorial,video,reference"
        />
        <meta name="author" content="Wes Chua Swee Kwang" />
        <link
          href={Constant.address + '/reference/swift/range'}
          rel="canonical"
        />
        <link href={Constant.address} rel="home" />
        <meta name="robots" content="index, follow" />
      </Helmet>

      <WithSidebar
        data={Swift}
        headerTitle="Swift Reference"
        headerURL="/reference/swift"
        activate={[2, 12]} // Section, Row
        state={props.state}
      >
        <h1 className="subHeader">Swift Range or ClosedRange</h1>
        <h2 className="header">What is a Swift Range and how to create?</h2>
        <AuthorInfo version="Xcode 12.2, Swift 5.3" />
        <p>
          Range or ClosedRange able you to generate a sequence of countable
          integers or float. This is like a array of integers or float. For
          example, if you want to create an array of value such as 0, 1, 2, 3,
          4, 5. Instead of typing it out one at a time. You can use to range to
          generate for you.
        </p>

        <h4>Topics:</h4>
        <ol>
          <li>
            <a href="/reference/swift/sets#create">How to create a Range?</a>
          </li>
          <li>
            <a href="/reference/swift/sets#loop">
              Combining Range with For Loop
            </a>
          </li>
          <li>
            <a href="/reference/swift/arrays#inspecting">Inspecting a Range</a>
          </li>
        </ol>

        <h2 id="create" className="header2">
          Creating a Range
        </h2>
        <h3>Closed Range</h3>
        <p>
          You can create a closed range by using <strong>(...)</strong>.
        </p>
        <p>For example, 0...5 will produce value from 0 to 5 inclusive.</p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeInt</span> ={' '}
              <span className="darkblue">0</span>...
              <span className="darkblue">5</span>
              <br />
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeDouble</span> ={' '}
              <span className="darkblue">0.0</span>...
              <span className="darkblue">1.1</span>
              <br />
            </Fragment>
          }
        />

        <h3>Half-open Range</h3>
        <p>
          You can replace the three dots with two dots plus a less than sign{' '}
          <strong>(&lt;)</strong>. Half-open means that the range goes from the
          first value to the last one but not including.
        </p>
        <p>
          For example, 0...5 will produce value from 0 to 5 but not including 5.
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeInt</span> ={' '}
              <span className="darkblue">0</span>..&lt;
              <span className="darkblue">5</span>
              <br />
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeDouble</span> ={' '}
              <span className="darkblue">0.0</span>..&lt;
              <span className="darkblue">1.1</span>
              <br />
            </Fragment>
          }
        />

        <p>
          Both open and half-open ranges must be increasing and the second
          number must always be greater than the first value.
        </p>

        <h2 id="loop" className="header2">
          Combining Range with For-Loop
        </h2>
        <p>
          Coutable ranges are commonly used with for loops because this generate
          a range of value for the loop to loop throught.
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeInt</span> ={' '}
              <span className="darkblue">0</span>..&lt;
              <span className="darkblue">5</span>
              <br />
              <span className="darkpurple">for</span> i{' '}
              <span className="darkpurple">in</span>{' '}
              <span className="blue">rangeInt</span> &#123; <br />
              &#125;
            </Fragment>
          }
          result="0 1 2 3 4"
        />

        <h2 id="inspecting" className="header2">
          Inspecting a Range
        </h2>
        <p>
          You can inspect the range to get information about it such as finding
          the number of value by using count, finding the lower and upper bound.
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">rangeInt</span> ={' '}
              <span className="darkblue">0</span>..&lt;
              <span className="darkblue">5</span>
              <br />
            </Fragment>
          }
        />
        <h3>.count</h3>
        <SwiftCode
          code={<Fragment>print(rangeInt.count)</Fragment>}
          result="5"
        />

        <h3>.lowerBound</h3>
        <SwiftCode
          code={<Fragment>print(rangeInt.lowerBound)</Fragment>}
          result="0"
        />

        <h3>.upperBound</h3>
        <SwiftCode
          code={<Fragment>print(rangeInt.upperBound)</Fragment>}
          result="5"
        />
      </WithSidebar>
    </Fragment>
  );
};

export default Arrays;
