import React, { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Constant from '../../../../../Constant';

import WithSidebar from '../../../../../Container/WithSidebar/WithSidebar';
import AuthorInfo from '../../../../../Components/AuthorInfo/AuthorInfo';
import Swift from '../Swift';
import SwiftCode from '../../../../../Components/SwiftCode/SwiftCode';
import Image from '../../../../../Components/UI/Image/image';

const WhileLoop = (props) => {
  return (
    <Fragment>
      <Helmet>
        <title>How to create while loop in Swift? | iOS Development</title>
        <meta name="description" content="" />
        <meta
          name="keywords"
          content="comment,documentation,create,iOS,swift,tutorial,reference"
        />
        <meta name="author" content="Wes Chua Swee Kwang" />
        <link
          href={Constant.address + '/reference/swift/whileloop'}
          rel="canonical"
        />
        <link href={Constant.address} rel="home" />
        <meta name="robots" content="index, follow" />
      </Helmet>

      <WithSidebar
        data={Swift}
        headerTitle="Swift Reference"
        headerURL="/reference/swift"
        activate={[2, 10]} // Section, Row
        state={props.state}
      >
        <h1 className="subHeader">How to create while loop in Swift? </h1>
        <h2 className="header">What is while loop?</h2>
        <AuthorInfo version="Xcode 12.2, Swift 5.3" />
        <p>
          While loop is a way of executing code mutiple times with repeating
          your code. Another type of loop is <a href="/">For loop</a> which will
          not be cover here. If you know other programming language, the
          concepts will be the same.
        </p>

        <p>
          <strong>Note: </strong>Before you start you need to know{' '}
          <a href="/reference/swift/comparsion">
            <strong>Boolean</strong>,<strong>comparsion</strong> and{' '}
            <strong>logical</strong>
          </a>
        </p>

        <h3>While Loop Syntax</h3>
        <p>
          A while loop reapets itself if the condition is true. To create a
          while loop, you need to use the <strong>while</strong> keyword and
          curly bracket. Between the bracket you add the code you want to
          repeat.
        </p>

        <p>
          CONDITION can be comparsion when the outcome is either true or false.
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">while</span>{' '}
              <span className="darkpurple">CONDITION</span> &#123; <br />
              &nbsp;&nbsp;&nbsp; <span className="grey">// run code</span>
              <br />
              &#125;
            </Fragment>
          }
        />

        <p>
          The loop will check for condition for every iteration. If the
          condition is true, the loop excutes and moves on to another iteration
          and stop when it is false. Refer to the image below:{' '}
        </p>
        <Image
          src="https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/While%20Loop/whileloop.png?raw=true"
          alt="While Loop in Swift"
          size="W200"
        />

        <p>
          As shown on the image, an while loop need to have a way to end the
          loop if not. It will result in an <strong>infinite loop</strong> and
          infinite loop might not crash your program but it might freeze your
          computer. Look a the example below:{' '}
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">number</span> ={' '}
              <span className="darkblue">1</span> <br />
              <span className="darkpurple">while</span> number &gt;{' '}
              <span className="darkblue">0</span> &#123; <br />
              &nbsp;&nbsp;&nbsp; <span className="purple">print</span>(
              <span className="red">"run"</span>) <br />
              &#125;
            </Fragment>
          }
        />
        <p>
          The code causes an <strong>infinite</strong> loop because the number
          will always be 1 and number is always bigger than zero. You can write
          an if statement inside to end it. The example code below will end the
          loop by resting the number to zero.{' '}
          <i>(Of course, another way is to reduce the number in each loop.)</i>
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">let</span>{' '}
              <span className="blue">number</span> ={' '}
              <span className="darkblue">1</span> <br />
              <span className="darkpurple">while</span> number &gt;{' '}
              <span className="darkblue">0</span> &#123; <br />
              &nbsp;&nbsp;&nbsp;<span className="darkpurple">if</span> number =={' '}
              <span className="darkblue">5</span> &#123; <br />
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;number ={' '}
              <span className="darkblue">0</span> <br />
              &nbsp;&nbsp;&nbsp; &#125; <br />
              &nbsp;&nbsp;&nbsp; number += <span className="darkblue">
                1
              </span>{' '}
              <br />
              &nbsp;&nbsp;&nbsp; <span className="purple">print</span>(
              <span className="red">"run"</span>) <br />
              &#125;
            </Fragment>
          }
        />

        <h3>Repeat while loop</h3>
        <p>
          The concept for repeat while loop is the same as the one above but the
          loop evaluated the condition at the <strong>end</strong> of the loop
          instead of <strong>beginning.</strong>
        </p>

        <p>
          By doing this, the loop will always run once even if the condition is
          not true.
        </p>
        <SwiftCode
          code={
            <Fragment>
              <span className="darkpurple">var</span>{' '}
              <span className="blue">number</span> ={' '}
              <span className="darkblue">5</span> <br />
              <span className="darkpurple">repeat</span> &#123; <br />
              &nbsp;&nbsp;&nbsp; <span className="blue">number</span> -={' '}
              <span className="darkblue">1</span> <br />
              &#125; <span className="darkpurple">while</span> number &gt;{' '}
              <span className="darkblue">0</span>
            </Fragment>
          }
        />
      </WithSidebar>
    </Fragment>
  );
};

export default WhileLoop;
